import testsBase from "./TestsBase";

const TestsCard = () => {
  return (
    <>
      {testsBase.map(
        ({
          aos,
          aosDelay,
          id,
          imgUrl,
          title,
          info,
          linkTest,
          countTest,
          answersTest,
        }) => (
          <div
            className="col-lg-4 col-md-6 col-12 mb-md-5 mb-4 tests-card"
            data-aos={aos}
            data-aos-duration="800"
            data-aos-delay={aosDelay}
            data-aos-once="true"
            key={id}
          >
            <div className="img-box">
              <img src={imgUrl} alt={title} />
            </div>
            <div className="text-box">
              <p className="date">Testlar soni {countTest} ta</p>
              <h3>{title}</h3>
              <p className="card-text">{info}</p>
              <a
                href={linkTest}
                target={linkTest === "no_test" ? "_self" : "_blank"}
                rel="noreferrer"
              >
                Testni boshlash
              </a>
              {countTest > 0 ? (
                <a href={answersTest} target={"_blank"} rel="noreferrer" className="answersTest">
                  Javoblar
                </a>
              ) : (
                ""
              )}
            </div>
          </div>
        )
      )}
    </>
  );
};

export default TestsCard;
